import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    named: '',
    pass: '',
  },
  mutations: {
    changeName(state, v) {
      state.named = v
    },
    changePass(state, v) {
      state.pass = v
    },
    loginOut(state, v) {
      state.named = ''
      state.pass = ''
      localStorage.setItem('invoice_user', '')
    }
  },
  actions: {

  },
  // 过滤获取东西
  getters: {
    newName(state) {
      return v => {
        return state.newName + '_' + v
      }
    },
    validateLogin(state) {

      const e = { named: 'support@manfulls.com', pass: '12345' }

      return v => {
        if (v.named != e.named) {
          return '无此电邮账户！'
        }
        if (v.pass != e.pass) {
          return '密码错误'
        }
        return true
      }
    },
    login(state) {
      let e = {}
      if ((state.named == null) || (state.named == "")) {
        const user = localStorage.getItem('invoice_user')
        if ((user == undefined) || (user == '') || (user == null)) {

          e = { named: 'support@manfulls.com', pass: '12345' }
          localStorage.setItem('invoice_user', JSON.stringify(e))

        } else {
          e = JSON.parse(user)
        }

        state.named = e.named
        state.pass = e.pass
      }
      return v => {
        return ''
      }
    },
    getUserName(state) {
      return v => {
        if (state.named == '') { return '未登录' }
        return state.named
      }
    }
  },
  modules: {

  }
})

export default store


