
const PricesView = () => import('@/screen/Prices/View/PricesView')
const PricesPlus = () => import('@/screen/Prices/PaU/PricesPlus')
const PricesUpdate = () => import('@/screen/Prices/PaU/PricesUpdate')

export default [

  {
    path: 'prices/',
    component: PricesView,
    meta: {
      title: '报价单列表'
    }
  },
  {
    path: 'prices_plus/',
    component: PricesPlus,
    meta: {
      title: '新增报价单'
    }
  },
  {
    path: 'prices_update/',
    component: PricesUpdate,
    meta: {
      title: '修改报价单'
    }
  },
]


