
const MemberView = () => import('@/screen/Member/View/MemberView')
const MemberPlus = () => import('@/screen/Member/Plus/MemberPlus')
const MemberUpdate = () => import('@/screen/Member/Update/MemberUpdate')

export default [
  
  {
    path: 'member/',
    component: MemberView,
    meta: {
      title: '客人'
    }
  },
  {
    path: 'member_plus/',
    component: MemberPlus,
    meta: {
      title: '新增客户'
    }
  },
  {
    path: 'member_update/',
    component: MemberUpdate,
    meta: {
      title: '修改客户'
    }
  },
]


