
const PricesPdfView = () => import('@/screen/Pdf/PrintPricesPdf')
const ListingPdfView = () => import('@/screen/Pdf/PrintListingPdf')

export default [

  {
    path: '/pdf_prices',
    component: PricesPdfView,
    meta: {
      title: '报价单'
    }
  },
  {
    path: '/pdf_listing',
    component: ListingPdfView,
    meta: {
      title: '发票'
    }
  }
]


