
const Dashboard = () => import('@/screen/Dashboard/Dashboard')


import ListingRouter from './dashboard/listing'
import MemberRouter from './dashboard/member'
import FreightRouter from './dashboard/freight'
import PricesRouter from './dashboard/prices'
import TagRouter from './dashboard/tag'


export default [
    {
      path: '',
      redirect: 'dashboard'
    },
    {
      path: 'dashboard/',
      component: Dashboard,
      meta: {
        title: '主面板'
      }
    },
    ...ListingRouter,
    ...MemberRouter,
    ...FreightRouter,
    ...PricesRouter,
    ...TagRouter
  ]


