import Router from 'vue-router'

import home from './home'
import auth from './auth/auth'
import print from './print/print'

const HomeScreen = () => import('@/screen/Home/Home')
const Test = () => import('@/screen/Extra/Test')

export default new Router({
  mode: 'history',
  linkActiveClass: 'menu-active',
  routes: [
    {
      path: '/home',
      component: HomeScreen,
      children: home
    },
    ...auth,
    ...print,
    {
      path: '',
      redirect: '/home',
      meta: {
        title: '首页'
      }
    },

    // 测试
    {
      path: '/test',
      component: Test,
      meta: {
        title: '测试'
      }
    }
  ]
})



